import Vue from 'vue'
import ElementUI from 'element-ui';
import 'element-ui/lib/theme-chalk/index.css';
import App from './App.vue'
import index from './components/index.vue'
import node from './components/node.vue'
import VueRouter from 'vue-router'
import WalletConnect from "@walletconnect/client";
import QRCodeModal from "@walletconnect/qrcode-modal";
import Web3 from "web3";
import { Loading } from 'element-ui';
import { Message } from 'element-ui';
import tp from 'tp-js-sdk';
Vue.use(ElementUI)
Vue.use(VueRouter)
Vue.config.productionTip = false
Vue.prototype.wallet = {
	address:'',
	web3:null,
	M_connector:null,
	tp:tp,
	created(){
		this.web3 = new Web3("https://http-mainnet-node.huobichain.com/");
		this.M_connector = new WalletConnect({
			bridge: "https://bridge.walletconnect.org", // Required
			qrcodeModal: QRCodeModal,
		});
		this.constructor_init(this.M_connector)
		if(this.M_connector.connected){
			this.address = this.M_connector._accounts[0]
		}
	},
	Transfer(value,to_address,data){
		const temp = this
		if (temp.address != ""){
			let loadingInstance = Loading.service({
				fullscreen: true,
				text: "Please wait a moment, the request is being sent"
			});
			console.log("no")
			const tx = {
				from: temp.address, // Required
				to: to_address, // Required (for non contract deployments)
				value: temp.web3.utils.toHex(temp.web3.utils.toWei(value))
			};
			if(data!=""){
				tx.data = temp.web3.eth.abi.encodeParameter('uint256', data)
			}
			console.log("yes")
			temp.web3.eth.getGasPrice(function(err, wei) {
				tx.gasPrice = temp.web3.utils.toHex(wei)
				temp.web3.eth.estimateGas(tx, function(err, gas) {
					if (err) {
						console.log(err)
						loadingInstance.close();
						Message.error({
							message: 'Unable to pay for gas'
						});
						return
					}
					tx.gas = temp.web3.utils.toHex(gas)
					if (temp.M_connector.connected) {
						temp.M_connector
							.sendTransaction(tx)
							.then((result) => {
								// Returns transaction id (hash)
								console.log(result);
								loadingInstance.close();
								Message({
									message: 'Successful trade',
									type: 'success'
								});
							})
							.catch((error) => {
								loadingInstance.close();
								Message.error({
									message: 'You turned down the deal'
								});
							});
					} else {
						ethereum.request({
								method: 'eth_sendTransaction',
								params: [tx],
						}).then(function(result) {
							console.log(result);
							loadingInstance.close();
							Message({
								message: 'Successful trade',
								type: 'success'
							});
						}).catch(function(reason) {
							loadingInstance.close();
							Message.error({
								message: 'You turned down the deal'
							});
						});
					}
				})
			})
		}
	},
	connectWallet() {
		const connector = new WalletConnect({
			bridge: "https://bridge.walletconnect.org", // Required
			qrcodeModal: QRCodeModal,
		});
		if (!connector.connected) {
			// create new session
			connector.createSession();
		}
		this.M_connector = connector
		this.constructor_init(connector)
	},
	constructor_init(connector) {
		connector.on("connect", (error, payload) => {
			console.log("connect")
			if (error) {
				throw error;
			}
			const {
				accounts,
				chainId
			} = payload.params[0];
			this.address = accounts[0]
		});
		connector.on("session_update", (error, payload) => {
			console.log("session_update")
			if (error) {
				throw error;
			}
			const {
				accounts,
				chainId
			} = payload.params[0];
		});
		connector.on("disconnect", (error, payload) => {
			console.log("disconnect")
			if (error) {
				throw error;
			}
			this.address = ""
		});
	},
	Query_PN(address,callback) {
		const abi = [{
			"constant": true,
			"inputs": [{
				"name": "_owner",
				"type": "address"
			}],
			"name": "balanceOf",
			"outputs": [{
				"name": "balance",
				"type": "uint256"
			}],
			"payable": false,
			"type": "function"
		}]
		const contract_address = "0x802Eb92dD5e5bcaB8970c8679FB2e2a3255C60f6"
		const contract = new this.web3.eth.Contract(abi, contract_address)
		contract.methods.balanceOf(address).call((err, result) => {
			callback(this.web3.utils.fromWei(result));
		})
	},
	Query_HT(address,callback) {
		var temp = this
		this.web3.eth.getBalance(address, function(err, res) {
			callback(temp.web3.utils.fromWei(res));
		})
	}
};



const router = new VueRouter({
	routes: [
		{ path: '/', component: index , meta: {title:'PN - Polar network'}},
		{ path: '/node', component: node , meta: {title:'PN - Polar network'}}
	]
})

new Vue({ 
  render: h => h(App),
  router
}).$mount('#app')